<template>
  <div class="video-wrap">
    <div id="yt-video">
      <youtube
        ref="youtube"
        :video-id="videoId"
        :player-vars="playerVars"
        :resize="true"
        :fit-parent="true"
        @playing="onPlay"
        @ended="onEnd"
      ></youtube>
    </div>
    <VideoProgress :duration="endTime || duration" :current-time="currentTime - startTime" />
    <VideoControls :mute-state="isMuted" :ended-state="hasEnded" @muteClicked="muteToggle" @replayClicked="replay" />
  </div>
</template>

<script>
const VideoProgress = () => import(/* webpackChunkName: "progress" */ '@/components/VideoProgress.vue')
const VideoControls = () => import(/* webpackChunkName: "controls" */ '@/components/VideoControls.vue')

export default {
  name: 'VideoComponent',

  components: {
    VideoProgress,
    VideoControls
  },

  data () {
    return {
      hasEnded: false,
      isMuted: true,
      playDelaySec: 0,
      duration: 0,
      currentTime: 0,
      videoId: this.ytVideoId,
      playerVars: {
        autoplay: 1,
        color: 'white',
        controls: 0,
        disablekb: 1,
        enablejsapi: 1,
        end: this.endTime,
        fs: 0,
        // loop: 1,
        modestbranding: 1,
        // playlist: this.ytVideoId,
        rel: 0,
        start: this.startTime
      }
    }
  },

  async mounted () {
    this.timeoutHandler = setTimeout(() => {
      this.muteToggle(true)
      this.playToggle(true)
    }, this.playDelaySec * 1000)

    this.currentTime = await this.player.getCurrentTime()
    this.timerHandler = setInterval(async () => {
      this.currentTime = await this.player.getCurrentTime()
    }, 125)
  },

  unmounted () {
    clearTimeout(this.timeoutHandler)
    clearInterval(this.timeoutHandler)
  },

  props: {
    ytVideoId: {
      type: String,
      default: null
    },
    startTime: {
      type: Number,
      default: 0
    },
    endTime: {
      type: Number,
      default: 0
    }
  },

  methods: {
    muteToggle (mute) {
      mute ? this.player.mute() : this.player.unMute()
      this.isMuted = mute
    },
    playToggle (play) {
      play ? this.player.playVideo() : this.player.pauseVideo()
    },
    replay () {
      this.player.seekTo(this.startTime)
      this.hasEnded = false
    },
    async onPlay () {
      this.duration = await this.player.getDuration()
      this.player.setLoop(true)
    },
    onEnd () {
      // TODO: Show picture to hide player
      this.hasEnded = true
      this.replay()
    }
  },

  computed: {
    player () {
      return this.$refs.youtube.player
    }
  }
}
</script>

